<template>
  <div>
    <span class="big_box">
      <!-- 房间号-入住时段 -->
      <section id="roomTime">
        <div class="roomBox">
          <div class="w20">房间号</div>
          <div class="select w70">
            <div class="c00">
              {{now_info.scaleName}}&ensp;
              <span>{{now_info.roomSn}}</span>
            </div>
            <img
              src="../../assets/right.png"
              alt
            />
          </div>
        </div>
        <div
          class="roomBox"
          @click="reset"
        >
          <div class="w20">入住时段</div>
          <div class="select w70">
            <div class="c00">现在入住</div>&ensp;至&ensp;
            <div class="c00">
              {{ now_info.end_time.substr(0,6) }}&ensp;共{{ now_info.num }}晚
              <br />
            </div>
            <img
              src="../../assets/right.png"
              alt
            />
          </div>
        </div>
      </section>
      <!-- 房费总价-押金 -->
      <section id="roomTime">
        <div class="roomBox">
          <div class="w20 c00">房费总价</div>
          <input
            v-if="opened_price == false"
            type="number"
            step="1"
            min="0"
            @keyup="priceTwoNum(dataForm.realAmount,'1')"
            class="f9"
            placeholder="请输入房费"
            v-model="dataForm.realAmount"
          />
          <input
            v-else
            readonly
            type="number"
            step="1"
            min="0"
            class="f9"
            @input="priceTwoNum(totlePrice,'2')"
            placeholder="请输入房费"
            v-model="totlePrice"
          />
          <div
            v-if="now_info.num > 1"
            @click="opened_price = !opened_price"
            class="deposit"
          >
            <span :style="{color:(opened_price==true)?'#000':''}">按天输入</span>&ensp;
            <img
              v-if="opened_price==true"
              class="abled-back"
              src="@/assets/btn_selected.png"
              alt
            />
            <div
              v-else
              style="width:18%;border-radius:10px;border:1px solid #c8c9cc;"
            >
            </div>
          </div>
          <div
            v-else
            class="deposit"
          ></div>
        </div>
        <div
          class="opened_box"
          v-if="opened_price == true"
          id="input"
        >
          <div
            style="line-height:40px;text-align:left;padding:0 18px;"
            v-for="(item,i) in dayList"
            :key="i"
          >
            <span>{{item.time | dataFilter('MM月DD日')}}</span>&ensp;&ensp;&ensp;
            <label>
              <input
                type="number"
                @input="priceTwoNum(i,'3')"
                placeholder="请输入房价"
                v-model="item.price"
              />
            </label>
          </div>
        </div>
        <div class="roomBox">
          <div class="w20 c00">免押金入住</div>
          <div
            @click="opened_deposit"
            class="deposit"
          >
            <img
              v-if="deposit==true"
              class="abled-back"
              src="@/assets/btn_selected.png"
              alt
            />
            <div
              v-else
              style="width:16px;height:16px;border-radius:10px;border:1px solid #c8c9cc;"
            >
            </div>
          </div>
        </div>
        <div
          class="roomBox"
          v-if="now_info.num > 1"
        >
          <div class="w20 c00">提前退房</div>
          <div
            @click="opened_roomPrice"
            class="deposit"
          >
            <span>{{bcakPrice==true?"不退房费":"可退房费"}}</span>&ensp;
            <img
              v-if="bcakPrice==false"
              class="abled-back"
              src="@/assets/btn_selected.png"
              alt
            />
          </div>
        </div>
      </section>
      <!-- 续住价格 -->
      <section
        id="roomTime"
        style="display:flex;justify-content:space-between;padding:10px 14px;"
      >
        <div class="w20 c00">续住价格</div>
        <div class="w70">
          <van-radio-group
            v-model="radio"
            :disabled='opened_price==false?false:true'
            direction="horizontal"
          >
            <van-radio name="-1">按日历价</van-radio>
            <van-radio name="0">按设置价格</van-radio>
          </van-radio-group>
        </div>
      </section>
      <!-- 充值用电 -->
      <section
        id="roomTime"
        v-if="now_info.ammeter == 1"
      >
        <div class="roomBox display_center">
          <div class="w70 c00">
            <p style="text-align: left;">充值用电<br /><span class="c_88">开启后，顾客将需要充值才能使用空调</span></p>
          </div>
          <div
            @click="btn_air_statu"
            class="deposit"
          >
            <img
              style="width:30px;"
              v-if="air_statu"
              src="../../assets/opened.png"
              alt
            />
            <img
              style="width:30px;"
              v-else
              src="../../assets/closed.png"
              alt
            />
          </div>
        </div>
      </section>
      <!-- 手机号-备注信息 -->
      <section id="roomTime">
        <div class="roomBox">
          <div
            class="w20"
            style="color:#000;font-weight:bold;"
          >
            联系电话
          </div>
          <input
            type="tel"
            class="ww2"
            maxlength="11"
            placeholder="输入手机号"
            v-model="dataForm.phone"
          />
        </div>
        <div style="text-align:left;font-size:12px;padding:4px 14px;">请确保预留手机号与客人微信或支付宝绑定手机号相同。</div>
      </section>
      <!-- 入住人信息 -->
      <section
        id="roomTime"
        style="margin-bottom:70px;"
      >
        <div class="roomBox">
          <div class="leftBox c00 w100">
            <div class="leftBox_1">
              <p class="w20">入住人信息</p>
              <div
                class="img_box w70"
                style="-border:1px solid red;"
              >
              </div>
            </div>
            <div class="f12 w90">若未填写名字身份证号，请线下手动上传公安系统</div>
          </div>
        </div>
        <div v-if="isline==false">
          <div
            v-for="(item,i) in info_box"
            :key="i"
            class="infoBox"
          >
            <div class="roomBox">
              <div class="leftBox w100">
                <div>
                  <p class="w20">
                    {{item.name_l}}
                  </p>
                  <input
                    :class="i!=0?'ww1':'ww2'"
                    type="text"
                    placeholder="请输入入住人姓名"
                    v-model="item.v_name"
                  />
                </div>
                <div>
                  <p class="w20">
                    {{item.ID_l}}
                  </p>
                  <input
                    class="f10"
                    :class="i!=0?'ww1':'ww2'"
                    type="text"
                    oninput="if(value.length>18)value=value.slice(0,18),alert('请检查身份证是否无误')"
                    placeholder="请输入身份证号码"
                    v-model="item.v_ID"
                  />
                </div>
              </div>
              <div
                class="img_box"
                @click="dealLiver(i)"
                v-if="i != 0"
              >
                <img
                  src="../../assets/two/dalete.png"
                  alt
                  class="bottomImg"
                />
              </div>
            </div>
          </div>
        </div>
        <div
          v-if="isline==false"
          @click="addLiver"
          class="addLiver"
        >+&ensp;添加入住人</div>
      </section>
      <!-- 按钮 -->
      <div
        class="set"
        @click="sure_live"
      >确定入住</div>
      <!-- 产品房型 -->
      <mt-popup
        v-model="show_scaleName"
        class="scaleNameBox"
        position="bottom"
      >
        <div
          v-for="(item,i) in PorList"
          :key="i"
          id="colorBox"
          @click="choose_scaleName(item)"
        >
          <div>
            <p>{{item.scaleName}}</p>
          </div>
        </div>
      </mt-popup>
    </span>
  </div>
</template>
<script>
import { MessageBox, Toast } from 'mint-ui';
import { GetBusinessVirtualRoomscaleNowStateByroomscale, PostBusinessOrderHandleRoom } from '@/api/api_business_hotel_and_scale';
import dayjs from 'dayjs';
import { validRange } from 'semver';
export default {
    data() {
        return {
            now_info: '',
            o_deposit: '',
            deposit: false,
            remark: '',
            orderSourceMap: [],
            PorList: [],
            payMent: [],
            show_payment: false,
            splitNum: '',
            c_hotel: '',
            isline: false,
            info_box: [
                {
                    name_l: '真实姓名',
                    v_name: '',
                    ID_l: '身份证号',
                    v_ID: ''
                }
            ],
            aaa: [],
            pay_info: [
                {
                    a: '哎哟有房收款',
                    b: '生成二维码，顾客扫码登录后，即可支付房费及押金',
                    pic: require('../../assets/two/online.png')
                },
                {
                    a: '线下收款',
                    b: '酒店自行收取现金，不经过哎哟有房平台收款，密码通过短信发送',
                    pic: require('../../assets/two/outline.png')
                }
            ],
            show_dayprice: false,
            day_: '',
            day_price: '',
            dayPriceInfo: '',
            show_payment_outline: false,
            disCount: [],
            count_deposit: false,
            PorList: [],
            scaleName: '',
            show_scaleName: false,
            phone: '',
            bcakPrice: false,
            radio: '-1',
            dataForm: {
                realAmount: null,
                phone: '',
                roomUser: [],
                description: ''
            },
            air_statu: false,
            opened: false,
            dayList: null,
            opened_price: false
        };
    },
    watch: {
        dataForm: {
            handler: function (newVal) {
                window.sessionStorage.setItem('handlerRoom', JSON.stringify(this.dataForm));
            },
            deep: true
        },
        opened_price(olld, neww) {
            if (neww == false) {
                this.radio = '-1';
            }
        }
    },
    computed: {
        totlePrice() {
            var sum = null;
            sum = this.dayList.reduce((total, currentValue, currentIndex, arr) => {
                return total + Number(currentValue.price);
            }, 0);
            return sum;
        }
    },
    created() {
        window.sessionStorage.getItem('handlerRoom') ? (this.dataForm = JSON.parse(window.sessionStorage.getItem('handlerRoom'))) : '';
        this.height = window.screen.height;
        this.$store.commit('setHeader', '创建订单');
        this.now_info = JSON.parse(localStorage.getItem('split_info'));
        this.c_hotel = JSON.parse(localStorage.getItem('c_hotel'));
        if (this.c_hotel instanceof Array == false) {
            this.c_hotel = JSON.parse(localStorage.getItem('c_hotel'));
        } else {
            let c_hotel = JSON.parse(localStorage.getItem('c_hotel'));
            this.c_hotel = c_hotel[0];
        }
        this.getScaleName();
    },
    methods: {
        getScaleName() {
            // 产品房型
            let data = {
                hotelId: this.now_info.hotelId,
                diffDay: this.now_info.num
            };
            GetBusinessVirtualRoomscaleNowStateByroomscale(this.now_info.roomScaleId, data).done((res) => {
                if (res.data.length == 0) {
                    Toast({
                        message: '暂无产品房型,请重新选择',
                        duration: 1000
                    });
                } else {
                    res.data.sort(function (a, b) {
                        return b.freeNum > a.freeNum ? 1 : -1;
                    });
                    this.pro_room = res.data[0].id;
                    this.o_deposit = res.data[0].deposit;
                    this.o_deposit_o = res.data[0].deposit;
                    this.getDay(this.now_info.num);
                }
            });
        },
        // 根据入住天数获获取时间List
        getDay(num) {
            let currentH = dayjs().format('HH');
            this.dayList = [];
            for (let i = 0; i < num; i++) {
                let currentDay;
                if (Number(currentH) >= 0 && Number(currentH) <= 7) {
                    currentDay = dayjs().subtract(1, 'day').add(i, 'day').format('YYYY-MM-DD');
                } else {
                    currentDay = dayjs().add(i, 'day').format('YYYY-MM-DD');
                }
                let obj = {};
                obj.time = currentDay;
                obj.price = null;
                this.dayList.push(obj);
            }
        },
        // 显示或影藏
        lined() {
            this.isline = !this.isline;
        },
        // 入住时段
        reset() {
            setTimeout('window.history.go(-1)', 200);
        },
        // 显示产品房型
        showScaleName() {
            this.show_scaleName = true;
        },
        // 选择产品房型
        choose_scaleName(item) {
            this.scaleName = item.scaleName;
            this.pro_room = item.id;
            this.o_deposit = item.deposit;
            this.show_scaleName = false;
        },
        // 添加入住人
        addLiver() {
            var first_add = {
                name_l: '真实姓名',
                v_name: '',
                ID_l: '身份证号',
                v_ID: ''
            };
            this.info_box.push(first_add);
        },
        // 删除入住人
        dealLiver(i) {
            if (i != 0) {
                this.info_box.splice(i, 1);
            }
        },
        priceTwoNum(value, num) {
            if (num == '3') {
                for (let i = 0; i < this.dayList.length; i++) {
                    let ele = this.dayList[i];
                    if (value == i) {
                        ele.price = ele.price.toString();
                        ele.price = ele.price.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
                        ele.price = ele.price.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
                        ele.price = ele.price.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
                        ele.price = ele.price.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
                        if (ele.price.indexOf('.') < 0 && ele.price != '') {
                            //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                            ele.price = parseFloat(ele.price);
                        }
                    }
                }
            } else {
                value = value.toString();
                value = value.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
                value = value.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
                value = value.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
                value = value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
                if (value.indexOf('.') < 0 && value != '') {
                    //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                    value = parseFloat(value);
                }
                if (num == '1') {
                    this.dataForm.realAmount = value;
                } else if (num == '2') {
                    this.totlePrice = value;
                }
            }
        },
        // 免押金入住
        opened_deposit() {
            this.deposit = !this.deposit;
            if (this.deposit == true) {
                this.o_deposit = 0;
            } else {
                this.o_deposit = this.o_deposit_o;
            }
        },
        // 扫码用空调
        btn_air_statu() {
            this.air_statu = !this.air_statu;
        },
        // 可退房费
        opened_roomPrice() {
            if (this.bcakPrice == false) {
                MessageBox.confirm('', {
                    message: '开启后无法关闭',
                    title: '确认不退房费？',
                    cancelButtonText: '取消',
                    confirmButtonText: '确定'
                }).then((action) => {
                    if (action == 'confirm') {
                        this.bcakPrice = true;
                    }
                });
            }
        },
        // 确定入住
        sure_live() {
            if (!this.pro_room) {
                Toast({
                    message: '暂无产品房型,请重新选择',
                    duration: 900
                });
                return false;
            }
            let arr = [];
            for (var i = 0; i < this.info_box.length; i++) {
                if (this.info_box[i].v_ID && !/^[1-9]{1}[0-9]{14}$|^[1-9]{1}[0-9]{16}([0-9]|[xX])$/.test(this.info_box[i].v_ID)) {
                    Toast({
                        message: '请正确填写身份证号码',
                        duration: 900
                    });
                    return false;
                }
                let obj = {};
                obj.roomUser = this.info_box[i].v_name;
                obj.idCard = this.info_box[i].v_ID;
                arr.push(obj);
                this.arr = JSON.stringify(arr);
            }
            if (!this.dataForm.phone || (this.dataForm.phone && !/^1[3-9]\d{9}$/.test(this.dataForm.phone))) {
                Toast({
                    message: '请正确输入11位手机号码',
                    duration: 900
                });
                return false;
            }
            this.set_ok();
        },
        // 是否分房-确定
        set_ok() {
            MessageBox.confirm('', {
                message: '确定入住？',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (this.opened_price == true) {
                        let obj = {};
                        this.dayList.forEach((item) => {
                            obj[item.time] = item.price + '';
                        });
                        this.dayStr = JSON.stringify(obj);
                    }
                    if (action == 'confirm') {
                        this.ok_info = {};
                        this.ok_info.roomUser = this.isline == true ? '' : JSON.parse(this.arr);
                        this.ok_info.phone = this.dataForm.phone;
                        this.ok_info.diffDay = this.now_info.num;
                        this.ok_info.realAmount = this.opened_price == false ? Number(this.dataForm.realAmount) : Number(this.totlePrice);
                        this.ok_info.deposit = this.o_deposit;
                        this.ok_info.roomSn = this.now_info.roomSn;
                        this.ok_info.scaleName = this.now_info.scaleName;
                        this.ok_info.now_time = this.now_info.now_time;
                        this.ok_info.end_time = this.now_info.end_time;
                        let obj = {};
                        for (let i = 0; i < this.aaa.length; i++) {
                            obj[this.aaa[i].day] = this.aaa[i].price;
                        }
                        let priceRecord = JSON.stringify(obj);
                        this.dataForm.roomUser = this.isline == true ? '' : this.arr;
                        this.dataForm.phone = this.dataForm.phone;
                        this.dataForm.realAmount = this.opened_price == false ? Number(this.dataForm.realAmount) : Number(this.totlePrice);
                        this.dataForm.description = this.remark;
                        let data = Object.assign(
                            {
                                hotelId: this.now_info.hotelId,
                                roomScaleId: this.now_info.roomScaleId,
                                roomId: this.now_info.id,
                                virtualRoomScaleId: this.pro_room,
                                orderType: 1,
                                roomNum: 1,
                                diffDay: this.now_info.num,
                                deposit: this.o_deposit,
                                priceRecord: this.opened_price == true ? this.dayStr : '',
                                checkOutFree: this.bcakPrice == false ? '0' : '1',
                                elecPayModel: this.air_statu == false ? '0' : '1',
                                price: this.radio
                            },
                            this.dataForm
                        );
                        if (this.opened_price == true) {
                            delete data.price;
                        }
                        sessionStorage.removeItem('handlerRoom');
                        PostBusinessOrderHandleRoom(data).done((res) => {
                            this.ok_info.orderId = res.data;
                            this.$router.push({
                                path: '/quickLiveOnline',
                                query: {
                                    fenfangInfo: this.ok_info
                                }
                            });
                            localStorage.setItem('fenfangInfo', JSON.stringify(this.ok_info));
                        });
                        this.getScaleName();
                        this.now_info.num = JSON.parse(localStorage.getItem('split_info')).num;
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        }
    }
};
</script>
